@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400&family=Roboto:wght@400;500&display=swap');

html {
  margin: 0;
  padding: 0;
  color: rgb(66, 66, 66);
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100%;
}


ul, li, a {
  margin: 0;
  padding: 0;
}

* {
  font-family: Lato /* Replace with your custom font */, sans-serif !important;
  /* Add !important to overwrite all elements */
  box-sizing: border-box;
}
